<template>
  <div>
    <el-container>
      <el-header style="height: 5%"></el-header>
      <el-main style="display: flex; flex-direction: column">
        <div>
          <div
            style="
              display: flex;
              width: auto;
              flex-direction: column;
              align-items: center;
              justify-content: center;
            "
          >
            <el-input
              placeholder="请输入..."
              v-model="input3"
              style="width: auto"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="search"
              ></el-button>
            </el-input>
          </div>
        </div>
        <div style="height: 45px"></div>
        <div style="display: flex">
          <div style="width: 150px"></div>
          <div
            style="
              width: 250px;
              height: 750px;
              background: #ffffff;
              border-radius: 6px;
            "
          >
            <el-row
              class="tac"
              style="
                width: auto;
                height: 750px;
                background: #ffffff;
                border-radius: 6px;
              "
            >
              <el-col :span="24">
                <el-menu default-active="2" class="el-menu-vertical-demo">
                  <div style="height: 25px"></div>
                  <el-menu-item index="2">
                    <i class="el-icon-picture-outline-round"></i>
                    <span slot="title" @click="hand1">道具专区</span>
                  </el-menu-item>
                  <el-menu-item index="1">
                    <i class="el-icon-mouse"></i>
                    <span slot="title" @click="hand2">宠物专区</span>
                  </el-menu-item>

                  <el-menu-item index="3">
                    <i class="el-icon-s-custom"></i>
                    <span slot="title" @click="hand3">账号专区</span>
                  </el-menu-item>
                  <el-menu-item index="4">
                    <i class="el-icon-s-check"></i>
                    <span slot="title" @click="hand4">代练专区</span>
                  </el-menu-item>
                  <el-menu-item index="5">
                    <i class="el-icon-s-goods"></i>
                    <span slot="title" @click="hand5">求购专区</span>
                  </el-menu-item>
                  <el-menu-item index="6">
                    <i class="el-icon-setting"></i>
                    <span slot="title" @click="hand6">攻略专区</span>
                  </el-menu-item>

                  <el-menu-item index="7">
                    <i class="el-icon-setting"></i>
                    <span slot="title" @click="hand7">工具专区</span>
                  </el-menu-item>
                  <el-menu-item index="8">
                    <i class="el-icon-warning-outline"></i>
                    <span slot="title" @click="hand8">骗子专区</span>
                  </el-menu-item>
                </el-menu>
              </el-col>
            </el-row>
          </div>
          <div style="width: 60px"></div>
          <div
            style="
              top: 5%;
              right: 150px;
              flex: 1;
              background: #ffffff;
              border-radius: 6px;
            "
          >
            <div style="height: 25px"></div>
            <div style="display: flex">
              <div style="width: 40px"></div>

              <div>
                <el-tabs v-model="activeName" type="card">
                  <el-tab-pane> </el-tab-pane>
                  <el-tab-pane>
                    <template slot="label">
                      <el-button @click="hand11">人物道具专区</el-button>
                    </template>
                  </el-tab-pane>
                  <el-tab-pane>
                    <template slot="label">
                      <el-button @click="hand12">宠物道具专区</el-button>
                    </template>
                  </el-tab-pane>
                  <el-tab-pane>
                    <template slot="label">
                      <el-button @click="hand13">葫芦道具专区</el-button>
                    </template>
                  </el-tab-pane>
                </el-tabs>
              </div>
              <div style="flex: 1">
                <div>
                <el-select
                  v-model="ishot"
                  placeholder="请选择是否按照热度排行"
                  size="mini"
                >
                  <el-option label="热度排行" value="1"></el-option>
                  <el-option label="不按照热度排行" value="0"></el-option>
                </el-select>

                <el-button type="success" round @click="getHotRanking"
                  >热度排行</el-button
                ></div>

                <!-- <el-button type="primary" round="mini">倒叙排行</el-button> -->

                <el-input-number
                  v-model="number1"
                  :min="0"
                  :max="999999"
                  label="最小值"
                ></el-input-number>

                <el-input-number
                  v-model="number2"
                  :min="0"
                  :max="999999"
                  label="最大值"
                ></el-input-number>
                <el-button type="info" round @click="handleCalculate"
                  >计算</el-button
                >
              </div>
            </div>
            <div style="height: 20px"></div>
            <div style="display: flex">
              <div style="width: 55px"></div>
              <div
                style="width: 100%; height: 600px; flex: 1; overflow-y: auto"
              >
                <el-row :gutter="0">
                  <el-col
                    :span="5"
                    v-for="(cart, index) in carts.slice(0, 12)"
                    :key="cart.id"
                    :offset="index > 0 ? 1.8 : 0"
                    style="display: flex"
                  >
                    <el-card style="width: 100%; height: 100%">
                      <div style="padding: 0">
                        <div style="height: 50px; top: 0; left: 0; right: 0">
                          <img
                            src="../assets/tian/内容区底图.png"
                            @click="
                              showPopup(
                                require('../assets/tian/内容区底图.png')
                              )
                            "
                            style="
                              cursor: pointer;
                              width: 100%;
                              height: 80px;
                              top: 0;
                            "
                          />
                          <div v-if="isPopupVisible" class="popup">
                            <div class="popup-content">
                              <img :src="popupPhotoUrl" class="popup-photo" />
                              <span class="close-btn" @click="closePopup"
                                >&times;</span
                              >
                            </div>
                          </div>
                        </div>
                        <div style="height: 25px"></div>
                        <span>姓名:</span
                        ><span style="margin-right: 15px">{{ cart.name }}</span>
                        <span> 价格:</span><span>{{ cart.price }}</span>

                        <div class="bottom clearfix">
                          <div>
                            <span>QQ:</span><span>{{ cart.qq }}</span>
                          </div>
                        </div>
                      </div>
                    </el-card>
                  </el-col>
                </el-row>
              </div>
            </div>
            <div
              class="block"
              style="display: flex; flex-direction: column; margin-top: auto"
            >
              <el-pagination
                layout="prev, pager, next"
                :total="60"
                :page-size="pagesize"
                :current-page="currentPage"
                @size-change="handleSizeChange"
                @current-change="handlePageChange"
              >
              </el-pagination>
            </div>
          </div>

          <div style="width: 110px"></div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>
  <script>
import axios from "@/untils/axios";
import "@/assets/js/head";
export default {
  data() {
    return {
      carts: [],
      prev: 0,
      pager: 0,
      next: 0,
      input3: "",
      input:"",
      activeName: "first",
      number1: null,
      number2: null,
      num1:null,
      num2:null,
      ishot: '',
      currentPage: 1, //初始页
      pagesize: 12, //每页多少数据
      total: "",
      isPopupVisible: false,
      popupPhotoUrl: "", // 添加popupPhotoUrl属性
    };
  },
  methods: {
    fetchData() {
      axios.get("http://localhost:8081/ToolCharacter").then((res) => {
        console.log(res);
        this.carts = res.data.data;
      });
    },
    handleCalculate() {
      // alert(this.number1),
      // alert(this.number2),
      this.num1=this.number1;
      this.num2=this.number2
      axios
        .get("http://localhost:8081/ToolCharacterPage", {
          params: {
            BeginPrice: this.num1,
            EndPrice: this.num2,
            ishot: this.ishot,
            page: this.currentPage, // 将参数名改为 page
            pagesize: this.pagesize,
            name: this.input,
          },
        })
        .then((res) => {
          console.log(res);
          this.carts = res.data.data.rows;
        })

        .catch((error) => {
          console.error("请求错误：", error);
        });
    },
    getHotRanking() {
      axios
        .get("http://localhost:8081/ToolCharacterPage", {
          params: {
            BeginPrice: this.num1,
            EndPrice: this.num2,
            ishot: this.ishot,
            page: this.currentPage, // 将参数名改为 page
            pagesize: this.pagesize,
            name: this.input,
          },
        })
        .then((res) => {
          console.log(res);
          this.carts = res.data.data.rows;
        });
    },

    search() {
      this.input=this.input3;
      axios
        .get("http://localhost:8081/ToolCharacterPage", {
          params: {
            BeginPrice: this.num1,
            EndPrice: this.num2,
            ishot: this.ishot,
            page: this.currentPage, // 将参数名改为 page
            pagesize: this.pagesize,
            name: this.input,
          },
        })
        .then((res) => {
          console.log(res);
          this.carts = res.data.data.rows;
        });
    },
    handlePageChange(page) {
      this.currentPage = page;
      console.log(this.currentPage);
      axios
        .get("http://localhost:8081/ToolCharacterPage", {
          params: {
            BeginPrice: this.num1,
            EndPrice: this.num2,
            ishot: this.ishot,
            page: this.currentPage, // 将参数名改为 page
            pagesize: this.pagesize,
            name: this.input,
          },
        })
        .then((res) => {
          console.log(res);
          this.carts = res.data.data.rows;
          this.total = res.data.total;
        })
        .catch((error) => {
          console.error("获取数据时出错:", error);
        });
    },
    handleSizeChange: function (size) {
      this.pagesize = size;
      console.log(this.pagesize);
    },

    hand1() {
      this.$router.push("/");
    },
    hand2() {
      this.$router.push("/pet01");
    },
    hand3() {
      this.$router.push("/account");
    },
    hand4() {
      this.$router.push("/practise");
    },
    hand5() {
      this.$router.push("/qiu");
    },
    hand6() {
      this.$router.push("/lue");
    },
    hand7() {
      this.$router.push("/tool");
    },
    hand8() {
      this.$router.push("/pianzi");
    },
    hand11() {
      this.$router.push("/");
    },
    hand12() {
      this.$router.push("/proop02");
    },
    hand13() {
      this.$router.push("/proop03");
    },
    showPopup(path) {
      // 点击图片时触发弹窗效果
      this.popupPhotoUrl = path; // 设置弹窗图片路径
      this.isPopupVisible = true; // 显示弹窗
    },
    closePopup() {
      // 关闭弹窗
      this.isPopupVisible = false;
    },
  },
  mounted() {
    this.fetchData();
  },
};
import "../assets/css/head.css";
import "../assets/css/imagebag.css";
</script>
  <style>
</style>